
<script>
    import ObjectBaseField from "./ObjectBaseField.vue";

    let LinkField = {
        name: "LinkField",
        extends: ObjectBaseField,
        data() {
            return {};
        },
        computed: {
             model: {
                get() {
                    return this.value;
                },
                set(val) {
                    if (val.type=="page")
                        val.url = val && val.page ? val.page.url : "";
                    else if (val.type == "file")
                        val.url = val && val.file ? val.file : "";
                    else if (val.type=="email")
                        val.url = val && val.email ? 'mailto:'+val.email : "";
                    else if (val.type=="phone")
                        val.url = val && val.phone ? 'tel:'+val.phone : "";
                    
                    this.$emit("input", val);
                }
            },
            props() {
                let self = this;
                return {
                    schema: {
                        title: self.schema.title,
                        type: "object",
                        properties: {
                            text: {
                                title: "Text",
                                type: 'string'
                            },
                            type: {
                                title: "Type",
                                type: "string",
                                enum : ["url", "page", "file", "email", "phone"]
                            },
                            url: {
                                title: "Url",
                                type: "string"
                            },
                            page: {
                                title: "Page",
                                type: "string"
                            },
                            file: {
                                title: "File",
                                type: "string"
                            },
                            email: {
                                title: "Email",
                                type: "string"
                            },
                            phone: {
                                title: "Phone",
                                type: "string"
                            },
                            newWindow: {
                                type: "boolean"
                            }
                        }
                    },
                    options: {
                        fields: {
                            text: {
                                type: 'text',
                                multilanguage: self.options.multilanguage
                            },
                            type: {
                                type:"select"
                            },
                            url: {
                                type: 'text',
                                multilanguage: self.options.multilanguage,
                                dependencies:
                                {
                                    type: ["url"]
                                }
                            },
                            page: {
                                type: "page",
                                multilanguage: self.options.multilanguage,
                                dependencies:
                                {
                                    type: ["page"]
                                }
                            },
                            file: {
                                type: "file",
                                multilanguage: self.options.multilanguage,
                                dependencies:
                                {
                                    type: ["file"]
                                }
                            },
                            email: {
                                type: "text",
                                multilanguage: self.options.multilanguage,
                                dependencies:
                                {
                                    type: ["email"]
                                }
                            },
                            phone: {
                                type: "text",
                                multilanguage: self.options.multilanguage,
                                dependencies:
                                {
                                    type: ["phone"]
                                }
                            },
                            newWindow: {
                                rightLabel: "Open in new window",
                                dependencies:
                                {
                                    type: ["url", "page", "file"]
                                }
                            }
                        }
                    },
                    view: this.view,
                    connector: this.connector
                };
            }
        },
        methods: {},
        created() { },
        components: {},
        builder: {
            props() {
                return {
                    schema: {
                        type: "object",
                        properties: {
                            multilanguage: {
                                type: "boolean",
                            },
                        }
                    },
                    options: {
                        fields: {
                            multilanguage: {
                                rightLabel: "Multi language",
                            },
                        },
                    }
                };
            },
            fromBuilder(field) {
                return {
                    schema: {
                        title: field.label,
                        type: "object"
                    },
                    options: {
                        type: "link",
                        multilanguage: field.multilanguage,
                    }
                };
            },
            toBuilder(def) {
                return {
                    label: def.schema.title,
                    fieldType: "link",
                    multilanguage: def.options.multilanguage,
                };
            }
        }
    };
    export default LinkField;
</script>

